/***** GLOBAL *****/
body {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1525px;
}
.container.is-widescreen {
  max-width: 1173px;
}

section.bg-gradient1,
.bg-gradient1 {
  background: linear-gradient(180deg, #044484 0%, #1486c3 100%);
}

section.bg-gradient2,
.bg-gradient2 {
  background: linear-gradient(180deg, #1486c3 0%, #013879 100%);
}

p {
  margin-bottom: 20px;
}
@media screen and (max-width: 768px) {
  p {
    font-size: 16px;
  }
}

li {
  list-style: none;
  margin-bottom: 20px;
}

a {
  text-decoration: none;
  transition: all 0.25s ease-out;
}

