  background-color: transparent;
  color: #fff;
}
.dropdown .dropdown-menu .dropdown-content .dropdown-item {
  color: #062f61;
  font-size: 16px !important;
}
.dropdown .dropdown-menu .dropdown-content .dropdown-item a {
  color: #062f61;
  font-size: 16px !important;
}

