.card-1 {
  flex: 1;
  width: 100%;
  border-radius: 10px;
  text-align: center;
  background: linear-gradient(180deg, #044484 0%, #1486c3 100%);
  border-bottom: 7px solid #f8cf03;
  padding: 30px;
}
@media screen and (max-width: 768px) {
  .card-1 {
    padding: 24px;
  }
}
.card-1 .content .heading {
  color: #f8cf03;
  margin-bottom: 8px;
  font-size: 30px;
  font-weight: 800;
  line-height: 1.2;
}
@media screen and (max-width: 768px) {
  .card-1 .content .heading {
    font-size: 24px;
  }
}
.card-1 .content .subheading {
  font-size: 20px;
  max-width: 80%;
  margin: 0 auto;
}
@media screen and (max-width: 768px) {
  .card-1 .content .subheading {
    font-size: 16px;
  }
}
.card-1 .icon-text {
  justify-content: center;
}
.card-1 .icon-text .icon {
  margin-right: 0.3rem;
}

.card-2 {
  flex: 1;
  width: 100%;
  border-radius: 10px;
  background-color: #013879;
  padding: 1.3rem 1rem;
}
.card-2 nav.level {
  margin-bottom: 20px;
}
.card-2 nav.level .heading {
  font-size: 35px;
  font-weight: 700;
  line-height: 1.2;
}
@media screen and (max-width: 1499px) {
  .card-2 nav.level .heading {
    font-size: 28px;
  }
}
@media screen and (max-width: 768px) {
  .card-2 nav.level .heading {
    font-size: 24px !important;
  }
}
.card-2 nav.level .level-right .heading {
  display: inline-block;
  position: relative;
}
.card-2 nav.level .level-right .heading::before {
  content: url("https://api.iconify.design/bxs/down-arrow.svg?color=%23c9db00");
  position: absolute;
  top: -1.2rem;
  left: 50%;
  transform: translateX(-50%);
}
.card-2 .content {
  border-radius: 10px;
  min-height: 172px;
  padding: 1rem;
}
@media screen and (max-width: 768px) {
  .card-2 .content p {
    font-size: 16px;
  }
}
.card-2 .content.has-bg {
  background-color: rgba(255, 255, 255, 0.1);
}
.card-2 .content.is-grid-center {
  display: grid;
  place-items: center;
}
.card-2 .content.choose-a-task {
  padding: 0;
}
.card-2 .content.choose-a-task .columns {
  backdrop-filter: blur(5px);
  border: 1px solid transparent;
  border-radius: 10px;
  padding: 7px 17px;
  margin: 0 0 0.5rem 0;
  /*
  &:hover{
    background: rgba(255, 255, 255, 0.5);
    border: 1px solid $white;
    opacity: 0.8;

    .column{
     span{
        color: $white;
        background-color: $tertiary;
        opacity: 0.8;
      }
    }

  }
  */
}
@media screen and (max-width: 768px) {
  .card-2 .content.choose-a-task .columns {
    padding: 7px 10px;
  }
}
.card-2 .content.choose-a-task .columns .column {
  padding: 0;
}
.card-2 .content.choose-a-task .columns .column p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 0.5rem;
}
.card-2 .content.choose-a-task .columns.selected {
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #fff;
  opacity: 0.8;
}
.card-2 .content.choose-a-task .columns.selected .column.price span {
  color: #fff;
  background-color: #f8cf03;
}

.card-3 {
  flex: 1;
  width: 100%;
  background-color: #1486c3;
  border-radius: 10px;
  padding: 30px;
  text-align: center;
}
.card-3 figure {
  display: grid;
  place-items: center;
  margin-bottom: 1rem;
}

.button {
  border-radius: 8px;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 7px 23px;
  min-width: 122px;
  text-align: center;
}

.button.is-primary {
  background-color: #f8cf03;
  color: #000;
}
.button.is-primary:hover {
  background-color: #ebc403;
  color: #000;
}

.button.is-medium {
  font-size: 18px;
}
@media screen and (max-width: 768px) {
  .button.is-medium {
    font-size: 16px;
  }
}

.button.is-large {
  font-size: 22px;
  padding: 10px 23px;
}
@media screen and (max-width: 768px) {
  .button.is-large {
    font-size: 16px;
  }
}

.button.is-small {
  font-size: 16px;
}

.is-not-clickable {
  pointer-events: none;
}

