.hero {
  background: linear-gradient(180deg, #044484 0%, #1486c3 100%);
  position: relative;
}
.hero::before {
  content: "";
  background-image: url(../images/build/hero-curve-bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0;
  height: 90%;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .hero::before {
    background-image: none;
  }
}
