body {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1525px;
  &.is-widescreen {
    max-width: 1173px;
  }
}

section.bg-gradient1,
.bg-gradient1 {
  background: $gradient-1;
}
section.bg-gradient2,
.bg-gradient2 {
  background: $gradient-2;
}

p {
  margin-bottom: 20px;
  @include mobile {
    font-size: 16px;
  }
}

li {
  list-style: none;
  margin-bottom: 20px;
}

a {
  text-decoration: none;
  transition: all 0.25s ease-out;
}
