.dropdown {

    &.language-selector{
      .dropdown-menu {
        @include touch {
          left: unset;
          right: 0;
        }
      }
    }

    .dropdown-trigger {
      .button.is-primary {
        background-color: transparent;
        color: $white;
      }
    }

    .dropdown-menu{
      .dropdown-content{
        .dropdown-item {
          color: $primary-200;
          font-size: 16px !important;
          a {
            color: $primary-200;
            font-size: 16px !important;
          }
        }
      }
    }

}
 
  
.tag{
  width: 100%;

  @include mobile {
    font-size: 16px !important;
  }

  &.is-blue{
    background-color: $secondary;
    color: $light-green;
  }

  &.is-yellow{
    background-color: $tertiary;
    color: $white;
  }

}


figure.image{
 display: grid;
 place-items: center;
}


.accordions{
  background-color: transparent;

  .accordion:not(:first-child) .accordion-header {
    border-radius: 10px;
  }

  .accordion{
    margin-bottom: 1rem !important;
    border-radius: 10px;
    background-color: transparent;
    font-size: 24px;

    .accordion-header + .accordion-body{
      border: 0;
    }
    
    .accordion-header{
      background-color: $primary;
      justify-content: unset;
      border-radius: 10px;
      gap: 30px;
      padding: 20px 20px;

      .toggle{
        background-color: $secondary;
        border-radius: 10px;
        max-width: 40px;
        max-height: 40px;
        height: 40px;
        width: 40px;

        &::before{
          font-size: 1.5rem;
          transform: translateY(-50%);
          top: 50%;
          bottom: unset;
        }
      }
    }

    .accordion-body{
      background: #1C518F;
      border: 0;
      margin-top: -.3rem;

      .accordion-content{
        color: $white;
      }
  
    }

  }
}