/***** VARIABLES *****/
@import "./variables";

/***** BULMA FRAMEWORK *****/
@import "../../node_modules/bulma/sass/utilities/_all.sass";
@import "../../node_modules/bulma/sass/base/_all.sass";

@import "../../node_modules/bulma/sass/grid/columns.sass";

@import "../../node_modules/bulma/sass/layout/section.sass";

@import "../../node_modules/bulma/sass/elements/icon.sass";
@import "../../node_modules/bulma/sass/elements/tag.sass";
@import "../../node_modules/bulma/sass/elements/image.sass";

@import "../../node_modules/bulma/sass/components/dropdown.sass";
@import "../../node_modules/bulma/sass/components/navbar.sass";
@import "../../node_modules/bulma/sass/components/level.sass";

@import "../../node_modules/bulma/sass/helpers/spacing.sass";
@import "../../node_modules/bulma/sass/helpers/visibility.sass";
@import "../../node_modules/bulma/sass/helpers/flexbox.sass";
@import "../../node_modules/bulma/sass/helpers/typography.sass";

/***** GLOBAL *****/
@import "./global";

/***** BULMA COMPONENTS *****/
@import "bulma-components";

/***** COMPONENTS *****/
@import "components";

/***** BLOCKS *****/
header {
  .top-nav {
    padding: 0.75rem 1.5rem;
    background-color: $primary-200;
    font-size: 16px;
  }

  .main-nav {
    background-color: $primary;
    padding: 0.75rem 1.5rem;

    @include touch {
      padding: 0;
    }

    .navbar-container {
      @include touch {
        padding: 0.75rem 0.75rem 0 0.75rem;
      }

      .navbar {
        .navbar-brand {
          .navbar-burger {
            height: 2rem !important;
            width: 2rem !important;
          }

          @include touch {
            padding: 0.7rem 1rem;
          }

          align-items: center;
          .navbar-burger {
            margin-left: 0;
          }
        }

        .navbar-menu {
          @include touch {
            background: $gradient-3;
            padding: 0;
          }

          .customer-support-mobile {
            background-color: $primary-200;
            padding: 1rem 1.5rem;

            a {
              font-size: 16px;
            }
          }

          .navbar-end {
            justify-content: space-evenly !important;
            margin-left: unset !important;
            align-items: center !important;
            width: 100%;

            @include touch {
              background: $gradient-3;
              padding: 1.5rem;
            }

            li {
              list-style: none;
              margin: 0;

              @include touch {
                margin-bottom: 30px;

                &:last-child {
                  margin: 0;
                }
              }

              a {
                font-size: 24px;

                .icon-text {
                    align-items: center;
                
                    .nav-icon {
                      margin-right: 0.4em;
                      svg {
                        path {
                          transition: all 0.25s ease-out;
                        }
                      }
                    }
                
                    .icon {
                      svg {
                        path {
                          transition: all 0.25s ease-out;
                        }
                      }
                    }
                  }
                
                  &:hover {
                    color: $tertiary;
                
                    .icon-text {
                      .icon {
                        svg {
                          path {
                            fill: $tertiary;
                          }
                        }
                      }
                
                      .nav-icon {
                        svg {
                          path {
                            fill: $tertiary;
                          }
                        }
                      }
                    }
                  }

                @include until-widescreen {
                  font-size: 20px;
                }
              }
            }

            li.active {
              a {
                color: $tertiary;
                .icon-text {
                  .icon {
                    svg {
                      path {
                        fill: $tertiary;
                      }
                    }
                  }
                  .nav-icon {
                    svg {
                      path {
                        fill: $tertiary;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.hero {
  background: $gradient-1;
  position: relative;

  &::before {
    content: "";
    background-image: url(../images/build/hero-curve-bg.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
    height: 90%;
    width: 100%;

    @include mobile {
      background-image: none;
    }
  }

  .hero-content {
    position: relative;
    z-index: 10;
    padding: 169px 0 120px 0;

    @include mobile {
      padding: 70px 0 50px 0;
    }
  }

  .hero-text {
    position: relative;
    z-index: 2;
    text-align: center;

    @include mobile {
      padding: 0 21px;
      margin-bottom: 30px;
    }

    .title {
      margin-bottom: 13px;
      font-size: 60px;
      font-weight: 800;
      line-height: 81.71px;
      color: $white;
      font-family: $family-secondary;

      @include mobile {
        font-size: 24px;
        line-height: 32.68px;
      }
    }

    .top-lead {
      color: $tertiary-200;
      font-size: 20px;
      margin: 0;
      font-weight: 600;
      @include mobile {
        font-size: 14px;
        margin-bottom: 11px;
      }
    }

    .lead {
      margin-bottom: 25px;
      font-weight: 600;
      @include mobile {
        font-size: 14px;
      }
    }

    .yesterday-cashed-amount {
      margin-bottom: 30px;
      font-size: 60px;
      font-family: PoetsenOneFont;
      font-weight: 900;
      background: linear-gradient(180deg, #faea99 0%, #f8cf03 100%);
      background-clip: text;
      -webkit-text-fill-color: transparent;
      line-height: 1;
      @include mobile {
        font-size: 40px;
      }
    }
  }

  .cards {
    position: relative;
    z-index: 3;
    padding: 0 1rem 60px;
  }
}
.how-it-works {
  padding: 65px 1rem 60px;

  @include mobile {
    padding: 33px 1rem 30px;
  }

  .description {
    text-align: center;
    margin-bottom: 75px;

    @include mobile {
      margin-bottom: 40px;
    }
  }
  .title {
    font-size: 50px;
    font-weight: 800;
    line-height: 68.09px;
    font-family: $family-secondary;
    margin-bottom: 15px;
    @include mobile {
      font-size: 24px;
      line-height: 32.68px;
    }
  }
  .subtitle {
    font-size: 24px;
    font-weight: 400;
    line-height: 32.69px;
    @include mobile {
      font-size: 16px;
      line-height: 21.79px;
    }
  }
  .cards {
    margin-bottom: 40px;
  }
  .cta {
    text-align: center;
  }
}
.rewards-chest {
  padding: 69px 1rem 80px;

  .description {
    text-align: center;
    margin-bottom: 75px;
  }
  .title {
    font-size: 50px;
    font-weight: 800;
    line-height: 68.09px;
    font-family: $family-secondary;
    margin-bottom: 15px;
  }
  .subtitle {
    font-size: 24px;
    font-weight: 400;
    line-height: 32.69px;
  }

  .cards {
    padding: 0 4rem;
    @include touch {
      padding: 0;
    }
  }
}
.faqs {
  padding: 80px 1rem;

  .description {
    text-align: center;
    margin-bottom: 75px;
  }
  .title {
    font-size: 50px;
    font-weight: 800;
    line-height: 68.09px;
    font-family: $family-secondary;
    margin-bottom: 15px;
  }
}
.what-do-our-users-say {
  padding: 80px 1rem 167px;

  .description {
    text-align: center;
    margin-bottom: 75px;
  }

  .title {
    font-size: 50px;
    font-weight: 800;
    line-height: 68.09px;
    font-family: $family-secondary;
    margin-bottom: 15px;
  }

  .yesterday-users-chashout-banner {
    background-image: url("../images/build/bullseye.png");
    border: 4px solid $yellow;
    border-radius: 10px;
    background-color: $primary;
    background-size: 80%;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 430px;
    display: grid;
    place-items: center;
    position: relative;
    text-align: center;

    .content {
      .heading {
        margin-bottom: 0.3rem;
        font-weight: 600;
      }
      .yesterday-cashed-amount {
        font-weight: 900;
        background: linear-gradient(180deg, #faea99 0%, #f8cf03 100%);
        background-clip: text;
        -webkit-text-fill-color: transparent;
        line-height: 1;
        font-size: 94px;
      }
    }

    .graphic {
      position: absolute;
      bottom: -4rem;
      left: -4.5rem;
    }
  }

  .yesterday-users-chashout-text {
    background-color: #1486c3;
    text-align: center;
    min-height: 350px;
    display: grid;
    place-items: center;

    .content {
      .heading {
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 1.5rem;
        font-weight: 600;
        font-size: 44px;
        line-height: 59.93px;
      }
    }
  }
}
footer {
  .main-footer {
    background-color: $primary;
    padding: 50px 1rem 32px;

    .title {
      color: $tertiary;
      margin-bottom: 60px;
      font-size: 35px;
      font-weight: 700;
      line-height: 1.2;
    }

    .heading {
      font-weight: 800;
      margin-bottom: 40px;
    }

    .graphic-container {
      position: relative;

      @include mobile {
        display: none;
      }

      .graphic {
        position: absolute;
        bottom: -32px;
        left: 50%;
        transform: translateX(-50%);

        img {
          max-width: unset;
        }
      }
    }
    .graphic-container-mobile {
      position: relative;

      .graphic {
        @include tablet {
          display: none;
        }

        position: absolute;
        right: -8rem;
        bottom: 0;

        img {
          max-width: 80%;
        }
      }
    }

    .language-selector {
      margin-top: 70px;
      .language-selector-label {
        color: #ffffff99;
        margin-bottom: 19px;
      }
      .dropdown {
        .dropdown-menu {
          @include touch {
            left: unset;
            right: 0;
          }
        }

        .dropdown-trigger {
          .button {
            background-color: rgba(255, 255, 255, 0.1);
            border-radius: 10px;
            padding: 1px 20px;
          }
        }

        .dropdown-menu {
          .dropdown-item {
            a {
              font-size: 24px !important;
            }
          }
        }
      }
    }
  }

  .sub-footer {
    background-color: $primary-200;
    color: #ffffff80;
    padding: 30px;
    position: relative;
    z-index: 2;
  }
}
