//FONTS
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;500;600;700;800&family=Open+Sans:wght@800&display=swap");
@font-face {
  font-family: PoetsenOneFont;
  src: url("../font/PoetsenOne-Regular.ttf");
}

//BRANDS
$gradient-1: linear-gradient(180deg, #044484 0%, #1486c3 100%);
$gradient-2: linear-gradient(180deg, #1486c3 0%, #013879 100%);
$gradient-3: linear-gradient(180deg, #034384 0%, #1385c2 100%);

$blue: #013879;
$dark-blue: #062f61;
$white: #fff;
$black: #000;
$yellow: #f8cf03;
$dark-yellow: #ffb800;
$light-green: #c9db00;

$primary: #013879;
$primary-200: #062f61;
$secondary: #1486c3;
$tertiary: #f8cf03;
$tertiary-200: #ffb800;




//UPDATE BULMA VARIBLES
$family-primary: "Noto Sans", sans-serif;
$family-secondary: "Open Sans", sans-serif;
$family-tertiary: PoetsenOneFont;
$icon-text-spacing: 0.8em;
$body-color: $white;
$body-family: $family-primary;
$body-weight: 400;
$body-size: 24px;
$link: $white;
$link-hover: $tertiary;
$dropdown-item-color: $primary-200;
$dropdown-content-z: 50;
$tag-radius: 40px;
$section-padding: 3rem 21px;