//CARDS
.card-1 {
  flex: 1;
  width: 100%;
  border-radius: 10px;
  text-align: center;
  background: $gradient-1;
  border-bottom: 7px solid $tertiary;
  padding: 30px;
  @include mobile {
    padding: 24px;
  }

  .content {
    .heading {
      color: $tertiary;
      margin-bottom: 8px;
      font-size: 30px;
      font-weight: 800;
      line-height: 1.2;
      @include mobile {
        font-size: 24px;
      }
    }

    .subheading {
      font-size: 20px;
      max-width: 80%;
      margin: 0 auto;
      @include mobile {
        font-size: 16px;
      }
    }
  }

  .icon-text {
    justify-content: center;
    .icon {
      margin-right: 0.3rem;
    }
  }
}

.card-2 {
  flex: 1;
  width: 100%;
  border-radius: 10px;
  background-color: $primary;
  padding: 1.3rem 1rem;

  nav.level {
    margin-bottom: 20px;

    .heading {
      font-size: 35px;
      font-weight: 700;
      line-height: 1.2;

      @include until(1500px) {
        font-size: 28px;
      }
      
      @include mobile {
        font-size: 24px !important;
      }
    }

    .level-right {
      .heading {
        display: inline-block;
        position: relative;
        &::before {
          content: url("https://api.iconify.design/bxs/down-arrow.svg?color=%23c9db00");
          position: absolute;
          top: -1.2rem;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }

  .content {
    border-radius: 10px;
    min-height: 172px;
    padding: 1rem;

    p {
      @include mobile {
        font-size: 16px;
      }
    }

    &.has-bg {
      background-color: rgba(255, 255, 255, 0.1);
    }

    &.is-grid-center {
      display: grid;
      place-items: center;
    }

    &.choose-a-task {
      padding: 0;

      .columns {
        backdrop-filter: blur(5px);
        border: 1px solid transparent;
        border-radius: 10px;
        padding: 7px 17px;
        margin: 0 0 .5rem 0;
        @include mobile {
          padding: 7px 10px;
        }

        .column {
          padding: 0;

          p {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin: 0 .5rem;

          }
        }

        /*
        &:hover{
          background: rgba(255, 255, 255, 0.5);
          border: 1px solid $white;
          opacity: 0.8;

          .column{
           span{
              color: $white;
              background-color: $tertiary;
              opacity: 0.8;
            }
          }

        }
        */

        &.selected {
          background: rgba(255, 255, 255, 0.5);
          border: 1px solid $white;
          opacity: 0.8;
          .column.price {
            span {
              color: $white;
              background-color: $tertiary;
            }
          }
        }
      }
    }
  }
}

.card-3 {
  flex: 1;
  width: 100%;
  background-color: $secondary;
  border-radius: 10px;
  padding: 30px;
  text-align: center;

  figure {
    display: grid;
    place-items: center;
    margin-bottom: 1rem;
  }
}

//BUTTONS
.button {
  border-radius: 8px;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 7px 23px;
  min-width: 122px;
  text-align: center;
}

.button.is-primary {
  background-color: $tertiary;
  color: $black;
  &:hover {
    background-color: #ebc403;
    color: $black;
  }
}
.button.is-medium {
  font-size: 18px;
  @include mobile {
    font-size: 16px;
  }
}
.button.is-large {
  font-size: 22px;
  padding: 10px 23px;
  @include mobile {
    font-size: 16px;
  }
}
.button.is-small {
  font-size: 16px;
}

.is-not-clickable {
  pointer-events: none;
}
